.topology-default-create-connector {
  --create-connector-color: var(--pf-global--active-color--400);
  --create-connector-bg-color: var(--pf-global--BackgroundColor--light-100);
}

.topology-default-create-connector .topology-connector-arrow {
  stroke: var(--create-connector-color);
  fill: var(--create-connector-color);
}

.topology-default-create-connector__line {
  stroke: var(--create-connector-color);
  stroke-width: 2px;
  stroke-dasharray: 5px, 5px;
}

.topology-default-create-connector__create__bg {
  stroke: var(--create-connector-color);
  fill: var(--create-connector-bg-color);
  stroke-width: 2px;
}

.topology-default-create-connector__create__cursor {
  fill: var(--create-connector-color);
}

